import react from "react";
import { Container, ListGroup, ListGroupItem, Row, Col, Card } from "react-bootstrap";

const ProjectEntry = ({proj_title, technoglogies, desc, link}) => {
    const techList = technoglogies.map(function(obj, i){
        return <ListGroupItem>{obj}</ListGroupItem>
    });
    return (
        <Container>
            <Row>
                <Col xs={4}>
                    <h3>{proj_title}</h3>
                </Col>
                <Col>
                    <small>Find more at: <a>{link}</a></small>
                </Col>
            </Row>
            <Row>
                <Col xs={3}>                    
                    <Card>
                        <Card.Title>Technologies Used</Card.Title>
                        <Card.Body>
                            <ListGroup>
                                {techList}
                            </ListGroup>
                        </Card.Body>
                    </Card>
                </Col>
                <Col>
                    <Card>
                        <Card.Title>Description</Card.Title>
                        <Card.Body>
                            {desc}
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    )
}

export default ProjectEntry;