import react from "react";
import { Container } from "react-bootstrap";
import projectData from '../Data/projects.json'
import ProjectEntry from "./ProjectEntry";

const Projects = () => {
    const projEntries = projectData.map(function(entry, i){
        return <ProjectEntry desc={entry.description} proj_title={entry.title} technoglogies={entry.technologies_used} link={entry.link} />
    })
    return (
        <Container>
            <h1>Projects</h1>
            {projEntries}
        </Container>
    )
    
}

export default Projects;